import React from 'react'
import './Card.css';
import { Button } from '@material-ui/core';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';   
import ThumbDownIcon from '@material-ui/icons/ThumbDown';




function Card({ src, title, company, price, goodr, badr, years, tag1, tag2, tag3 }) {

    var send = function(tag) {
        var url = "http://wujiankai.pythonanywhere.com/".concat("?moviereview=", encodeURIComponent(tag));
        window.location.replace(url);
    };

    return (
        <div className='card'>
            <img src={src} alt="" />
            <div className="card__info">
                <h2>{title}</h2>
                <h4>{company}</h4>
                <div className="price__stars">
                    <h3>{price}</h3>
                    <div className="stars">
                        <h3>{goodr}</h3>
                        <ThumbUpIcon className='tu'/>
                        <h3>{badr}</h3>
                        <ThumbDownIcon className='td'/>
                    </div>
                </div>
                <h4 className='rev'> Since {years}</h4>
                <div className='ts'>
                    <Button onClick={(e) => e.target.addEventListener("click", send(tag1))}> {tag1} </Button>
                    <Button onClick={(e) => e.target.addEventListener("click", send(tag2))}> {tag2} </Button>
                    <Button onClick={(e) => e.target.addEventListener("click", send(tag3))}> {tag3} </Button>
                </div>
            </div>
        </div>
    )
}


export default Card
