import React from 'react'
import './Profiles.css'
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import { Button } from '@material-ui/core';


function Profile({
    img,
    years,
    name,
    company,
    description,
    languages,
    tag1,
    tag2,
    tag3,
    price,
    reviews
}) {

    var send = function(tag) {
        var url = "http://wujiankai.pythonanywhere.com/".concat("?moviereview=", encodeURIComponent(tag));
        window.location.replace(url);
    };
    
    return (
        <div className='profile'>
            <img src={img} alt="" />
            <MailOutlineIcon id="profile__mail" />

            <div className='profile__info'>
                <div className="profile__infoTop">
                    <h1>{name}</h1>
                    <p>{years} · {company}</p>
                    <p>____</p>
                    <p>{description}</p>
                    <p id='lan'>{languages}</p>
                </div>

                <div className="profile__infoBottom">
                    <div className="profile__reviews">
                        <p>Reviews: {reviews}</p>
                    </div>
                    <div className='profile__price'>
                        <div className='pts'>
                            <Button onClick={(e) => e.target.addEventListener("click", send(tag1))}> {tag1} </Button>
                            <Button onClick={(e) => e.target.addEventListener("click", send(tag2))}> {tag2} </Button>
                            <Button onClick={(e) => e.target.addEventListener("click", send(tag3))}> {tag3} </Button>
                        </div>
                        <h2>{price}</h2>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

export default Profile
