import React from 'react';
import "./Search.css";
import { Button } from '@material-ui/core';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import { useHistory } from "react-router-dom";

function Search() {

    const history = useHistory();

    var min = localStorage.getItem("min");
    var max = localStorage.getItem("max");

    var query = localStorage.getItem("query");
    
    if (query === 'null') {
        query = ''
    }

    function handleSearch() {

        history.push('/search');


        var min = document.getElementById("min").value;
        var max = document.getElementById("max").value;

        localStorage.setItem("min", min);
        localStorage.setItem("max", max);

        localStorage.setItem("elem", elem);
        localStorage.setItem("lang", lang);

        var query = document.getElementById("query").value;
        localStorage.setItem("query", query);

        var check = query + elem.toString();

        if(check.length===0){
            check = 'empty'
        }

        var url = "http://wujiankai.pythonanywhere.com/".concat("?moviereview=", encodeURIComponent(check));
        window.location.replace(url);
        
    }

    var elem = [];


    function pressed(bt) {
        var property = document.getElementById(bt);
        if (property.style.color !== "white"){
            property.style.backgroundColor = "#ff7779";
            property.style.color = 'white';
            elem.push(property.name);
            
        }
        else {
            property.style.backgroundColor = "#FCF0F5";
            property.style.color = 'black';
            var index = elem.indexOf(property.name);
            if (index > -1) {
                elem.splice(index, 1);
            }
        }
    }

    var lang = [];

    function pressed2(bt) {
        var property = document.getElementById(bt);
        if (property.style.color !== "black"){
            property.style.backgroundColor = "#d9ffd9";
            property.style.color = 'black';
            lang.push(property.name);
            
        }
        else {
            property.style.backgroundColor = "#3cb371";
            property.style.color = 'white';
            var index = elem.indexOf(property.name);
            if (index > -1) {
                lang.splice(index, 1);
            }
        }
    }

    return (
        <div className='search'>
            <div className='search__top'>
                <div className="search__problem">
                    <h1>Enter your query here</h1>
                    <textarea rows="10" cols="60" placeholder="Please keep your query to below 500 characters" defaultValue={query} id='query'></textarea>
                </div>

                <div className="search__tags"> 
                    <h1>Select all relevant tags</h1>

                    <Button onClick={() => pressed('b1')} id='b1' name='Employment'>Employment</Button>
                    <Button onClick={() => pressed('b2')} id='b2' name='Family'>Family</Button>
                    <Button onClick={() => pressed('b3')} id='b3' name='Harrassment'>Harrassment</Button>
                    <Button onClick={() => pressed('b4')} id='b4' name='Physical Hurt'>Physical Hurt</Button>
                    <Button onClick={() => pressed('b5')} id='b5' name='Divorce'>Divorce</Button>
                    <Button onClick={() => pressed('b6')} id='b6' name='Crime'>Crime</Button>
                    <Button onClick={() => pressed('b7')} id='b7' name='Neighbour'>Neighbour</Button>
                    <Button onClick={() => pressed('b8')} id='b8' name='Will'>Will</Button>
                    <Button onClick={() => pressed('b9')} id='b9' name='LPA'>LPA</Button>
                    <Button onClick={() => pressed('b10')} id='b10' name='White Collar Crime'>White Collar Crime</Button> 
                    <Button onClick={() => pressed('b11')} id='b11' name='Property'>Property</Button> 
                    <Button onClick={() => pressed('b12')} id='b12' name='Adoption'>Adoption</Button> 
                    <Button onClick={() => pressed('b13')} id='b13' name='Insurance'>Insurance</Button>
                    <Button onClick={() => pressed('b14')} id='b14' name='Drugs'>Drugs</Button>
                    <Button onClick={() => pressed('b15')} id='b15' name='Patent'>Patent</Button>
                    <Button onClick={() => pressed('b16')} id='b16' name='Contract'>Contract</Button>
                </div>
                
            </div>

            <div className='langs'>
                <h2>Preffered Language: </h2>
                <Button onClick={() => pressed2('l1')} id='l1' name='English'>English</Button>
                <Button onClick={() => pressed2('l2')} id='l2' name='Mandarin Chinese'>Mandarin</Button>
                <Button onClick={() => pressed2('l3')} id='l3' name='Malay'>Malay</Button>
                <Button onClick={() => pressed2('l4')} id='l4' name='Hindi'>Hindi</Button>
                <Button onClick={() => pressed2('l5')} id='l5' name='Spanish'>Spanish</Button>
                <Button onClick={() => pressed2('l6')} id='l6' name='Arabic'>Arabic</Button>
                <Button onClick={() => pressed2('l7')} id='l7' name='Laotian'>Laotian</Button>
                <Button onClick={() => pressed2('l8')} id='l8' name='Greek'>Greek</Button>
                <Button onClick={() => pressed2('l9')} id='l9' name='Japanese'>Japanese</Button>
                <Button onClick={() => pressed2('l10')} id='l10' name='Russian'>Russian</Button>
                <Button onClick={() => pressed2('l11')} id='l11' name='Yiddish'>Yiddish</Button>
                <Button onClick={() => pressed2('l12')} id='l12' name='Romani'>Romani</Button>
                <Button onClick={() => pressed2('l13')} id='l13' name='Hebrew'>Hebrew</Button>
                <Button onClick={() => pressed2('l14')} id='l14' name='Thai'>Thai</Button>
                <Button onClick={() => pressed2('l15')} id='l15' name='Vietnamese'>Vietnamese</Button>
                <Button onClick={() => pressed2('l16')} id='l16' name='Tagalog'>Tagalog</Button>
                <Button onClick={() => pressed2('l17')} id='l17' name='Bahasa Indonesia'>Bahasa Indonesia</Button>
                <Button onClick={() => pressed2('l18')} id='l18' name='Burmese'>Burmese</Button>
            </div>

            <h2>
                <AccountBalanceIcon />   Budget
                <input min={0} defaultValue={min} placeholder="100" type="number" name="min" id="min"/> to <input max={2000} defaultValue={max} name='max' id="max" placeholder="350" type="number" />per hour  
            </h2>

            <Button onClick={handleSearch}>
                Find My Lawyer
            </Button>
            
        </div>
    )
}

export default Search
