import './App.css'
import Home from './Home'
import Header from './Header';
import Footer from './Footer'; 
import SearchPage from './SearchPage';  

import DerrylChew from './DerrylChew';
import SofiaIbrahim from './SofiaIbrahim';
import NigelAw from './NigelAw';
import MuhammadBairos from './MuhammadBairos';
import TanYin from './TanYin';
import JudyWallace from './JudyWallace';
import Subhas from './Subhas';
import Lin from './Lin';
import Charlotte from './Charlotte';
import Randy from './Randy';
import Alex from './Alex';
import Teo from './Teo';

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  return (

    <div className="app">

      <Router>

        <Header />

        <Switch>

          <Route component={SearchPage} path="/search"/>

          <Route component={DerrylChew} path="/DerrylChew"/>
          <Route component={SofiaIbrahim} path="/SofiaIbrahim"/>
          <Route component={NigelAw} path="/NigelAw"/>
          <Route component={MuhammadBairos} path="/MuhammadBairos"/>
          <Route component={TanYin} path="/TanYin"/>
          <Route component={JudyWallace} path="/JudyWallace"/>
          <Route component={Subhas} path="/Subhas"/>
          <Route component={Lin} path="/Lin"/>
          <Route component={Charlotte} path="/Charlotte"/>
          <Route component={Randy} path="/Randy"/>
          <Route component={Alex} path="/Alex"/>
          <Route component={Teo} path="/Teo"/>

          <Route component={Home} path="/"/>

        </Switch>

        <Footer />
        
      </Router>

    </div>
  );
}

export default App;
