import React from 'react'
import './Header.css'
import SearchIcon from '@material-ui/icons/Search';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import LanguageIcon from '@material-ui/icons/Language';
import { Avatar } from "@material-ui/core"
import { Link } from "react-router-dom";
import { Button } from "@material-ui/core";

function Header() {

    function handleSearchbar() {


        if(document.getElementById("barquery")) {
            var barquery = document.getElementById("barquery").value;
            localStorage.setItem("barquery", barquery);

            var check = barquery;

            if(check.length===0){
                check = 'empty'
            }

            var url = "http://wujiankai.pythonanywhere.com/".concat("?moviereview=", encodeURIComponent(check));
            window.location.replace(url);
        }
    }

    return (
        <div className='header'>
            <Link to='/'>
                <img
                    className='header__icon'
                    src="https://i.imgur.com/tGIIMjm.jpeg"
                    alt=""
                />
            </Link>

            <div className='header__center'>
                <input type="text" id='barquery' placeholder="Look up law firms, practice areas..."/>
                <Button onClick={(e) => e.target.addEventListener("click", handleSearchbar())}><SearchIcon /></Button>
            </div>

            <div className='header__right'>
                <p>Register your legal services</p>
                <ExpandMoreIcon />
                <LanguageIcon />
                <Avatar />
            </div>
        </div>
    )
}

export default Header
