import React, { useState } from 'react'
import './Banner.css'
import { Button } from "@material-ui/core"
import Search from './Search';

function Banner() {

    const [showSearch, setShowSearch] = useState (false);

    return (
        <div className='banner'>
            <div className='banner__search'>
                {showSearch && <Search />}
                <Button onClick = {() => setShowSearch(!showSearch)} variant = 'outlined' className='banner__searchButton'>
                    {showSearch ? "Hide" : "Find Legal Representation"}
                </Button>
            </div>
            <div className='banner__info'>
                <h1>Looking for a lawyer suited to your needs?</h1>
                <h5>
                    Our site recommends lawyers according to your specific needs, language and cost preferences. Send in your queries now!
                </h5>
                <Button variant = 'outlined' onClick={() => setShowSearch(!showSearch)}>
                    {showSearch ? "Back" : "Find a Lawyer"}
                </Button>
            </div>
        </div>
    )
}

export default Banner
